import { useQuery } from "@tanstack/react-query"
import { useLocation } from "react-router-dom"
import { fetchCoinHistory } from "../api"
import ReactApexChart from "react-apexcharts"

interface ChartState{
    state:string
}
interface IData{
    close: string;
    high: string;
    low: string;
    market_cap: number;
    open: string;
    time_close: number;
    time_open: number;
    volume: string;
}
function Chart(){
    const {state} = useLocation() as ChartState
    const {isLoading,data} = useQuery<IData[]>({
        queryKey:['coinInfoHistory',state],
        queryFn:() => fetchCoinHistory(state),
        //5 refetch
        refetchInterval:5000
    })
    console.log('ChartChart',data)
    return(
        <>
            <div>{isLoading ? "Loading..." : 
                <ReactApexChart type="line" 
                series={[
                    {   
                        name:"price",
                        //  (?)  data undefined    
                        data:data?.map( i => Number(i.close)) ?? []
                    }
                ]} 
                options={{
                    theme:{
                        mode:"dark"
                    },
                    chart:{
                    height:300,
                    width:300,
                    toolbar:{
                        show:false
                        },
                    background:"transparent"
                    },
                    grid:{
                        show:false
                    },
                    yaxis:{
                        show:false
                    },
                    xaxis:{
                        labels:{
                            show:false
                        },
                        axisTicks:{
                            show:false
                        },
                        axisBorder:{
                            show:false
                        },
                        categories:data?.map(i => new Date(i.time_close * 1000).toUTCString())
                    },
                    stroke:{
                        curve:"smooth",
                        width:4
                    },
                    fill:{
                        type:"gradient",
                        gradient:{
                            gradientToColors:['blue'], stops:[0,100]

                        }
                    },
                    colors:['red'],
                    tooltip:{
                        y:{
                            formatter:(v) => `$ ${v.toFixed(2)}`
                        }
                    }
                }}/>}
            </div>
        </>
    )
}
export default Chart